(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/iml/dashboard/assets/html/server-dashboard.html',
    '<h4 class="section-header">Server: {{dashboard.server.label}}</h4><h4 class="section-header">Charts</h4><ng-include src="\'iml/dashboard/assets/html/charts.html\'"></ng-include>');
}]);
})();
