angular.module('mds').factory('MdsStream', ['stream', 'mdsTransformer', 'streamDurationMixin',
  'spliceOldDataTransformer', 'appendOrReplaceDataTransformer', mdsStreamFactory]);

function mdsStreamFactory(stream, mdsTransformer, streamDurationMixin,
                          spliceOldDataTransformer, appendOrReplaceDataTransformer) {
  'use strict';

  var MdsStream = stream('host', 'httpGetMetrics', {
    params: {
      qs: {
        reduce_fn: 'average',
        role: 'MDS',
        metrics: 'cpu_total,cpu_user,cpu_system,cpu_iowait,mem_MemFree,mem_MemTotal'
      }
    },
    transformers: [spliceOldDataTransformer, mdsTransformer, appendOrReplaceDataTransformer]
  });

  _.extend(MdsStream.prototype, streamDurationMixin);

  return MdsStream;
}
