(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/iml/notification/assets/html/status.html',
    '<a id="status_menu" class="navigation status-nav" href="/ui/status/" target="_self"><span ng-controller="NotificationCtrl" ng-class="notification.status.health" class="ng-cloak badge">{{notification.count}}<sup ng-if="notification.aboveLimit">+</sup></span> Status</a>');
}]);
})();
