angular.module('objectStorageServers', ['primus', 'charts'])
  .controller('ObjectStorageServersCtrl', ['$scope', 'MdsStream', 'DURATIONS', ObjectStorageServersCtrl]);

function ObjectStorageServersCtrl($scope, MdsStream, DURATIONS) {
  'use strict';

  $scope.objectStorageServers = {
    data: [],
    onUpdate: function (unit, size) {
      mdsStream.setDuration(unit, size);
    },
    options: {
      setup: function(chart, d3) {
        chart.useInteractiveGuideline(true);

        chart.forceY([0, 1]);

        chart.yAxis.tickFormat(d3.format('.1%'));

        chart.xAxis.showMaxMin(false);

        chart.color(['#F3B600', '#0067B4']);
      }
    },
    unit: DURATIONS.MINUTES,
    size: 10
  };

  var params = _.merge({
    qs: {
      role: 'OSS'
    }
  }, $scope.params || {});

  var mdsStream = MdsStream.setup('objectStorageServers.data', $scope, params);

  mdsStream.setDuration($scope.objectStorageServers.unit, $scope.objectStorageServers.size);
}
