(function () {
  'use strict';

  angular.module('dashboard')
    .controller('ServerDashboardCtrl', ['$scope', 'streams', 'dashboardPath', ServerDashboardCtrl]);

  function ServerDashboardCtrl($scope, streams, dashboardPath) {

    $scope.dashboard = {
      server: {},
      charts: [
        {name: 'iml/read-write-bandwidth/assets/html/read-write-bandwidth.html'},
        {name: 'iml/cpu-usage/assets/html/cpu-usage.html'},
        {name: 'iml/memory-usage/assets/html/memory-usage.html'}
      ]
    };

    $scope.params = {
      id: dashboardPath.getServerId()
    };

    $scope.readWriteBandwidthParams = {
      qs: {
        host_id: dashboardPath.getServerId()
      }
    };

    var hostStream = streams.hostStream('dashboard.server', $scope);

    hostStream.start({
      id: dashboardPath.getServerId(),
      jsonMask: 'label'
    });
  }
}());
