(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/common/tooltip/assets/html/help-tooltip.html',
    '<div class="help-tooltip"><iml-tooltip ng-if="has-toggle" toggle="toggle" direction="{{ direction }}"><span ng-bind-html="message"></span></iml-tooltip><iml-tooltip ng-if="!has-toggle" direction="{{ direction }}"><span ng-bind-html="message"></span></iml-tooltip></div>');
}]);
})();
