(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/iml/dashboard/assets/html/fs-dashboard.html',
    '<h4 class="section-header">File System: {{dashboard.fs.label}}</h4><div class="row dashboard-info"><div class="col-lg-12"><table class="table table-condensed"><thead><tr><th>Management Server (MGS)</th><th>Metadata Server (MDS)</th><th>Total Object Storage Targets (OST)</th><th>Space Used / Total</th><th>Files Used / Total</th><th>Clients Connected</th></tr></thead><tbody><tr><td>{{dashboard.fs.mgt.primary_server_name}}</td><td>{{dashboard.fs.mdts[0].primary_server_name}}</td><td>{{dashboard.fs.osts.length}}</td><td ng-if="dashboard.fs.bytes_total != null"><pie-graph chart-data="dashboard.fs.spaceGraphData"></pie-graph>{{ dashboard.fs.getUsedSpace() }} / {{ dashboard.fs.getTotalSpace() }}</td><td ng-if="dashboard.fs.bytes_total == null">Calculating...</td><td ng-if="dashboard.fs.files_total != null"><pie-graph chart-data="dashboard.fs.usageGraphData"></pie-graph>{{ dashboard.fs.getUsedFiles() }} / {{ dashboard.fs.getTotalFiles() }}</td><td ng-if="dashboard.fs.files_total == null">Calculating...</td><td>{{dashboard.fs.client_count }}</td></tr></tbody></table></div></div><h4 class="section-header">Charts</h4><ng-include src="\'iml/dashboard/assets/html/charts.html\'"></ng-include>');
}]);
})();
