(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/iml/job-stats/assets/html/job-stats-table.html',
    '<div class="col-md-6"><h3>{{ metricName }}</h3><table class="table" ng-if="metric.length > 0"><tr><th>#</th><th>Job ID</th><th ng-if="filterBytes">Throughput</th><th ng-if="!filterBytes">IOPS</th></tr><tr ng-repeat="item in metric | orderBy:\'-average\' | limitTo: 5"><td>{{ $index + 1 }}</td><td>{{ item.name }}</td><td ng-if="filterBytes">{{ item.average | throughput }}</td><td ng-if="!filterBytes">{{ item.average | round:3 }} IOPS</td></tr></table><h4 class="text-center" ng-if="!metric.length > 0">No Data found.</h4></div>');
}]);
})();
