(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/common/full-screen/assets/html/full-screen-button-text.html',
    '<span>{{ fullScreen.text() }} <i class="fa" ng-class="{\'fa-expand\': !fullScreen.open, \'fa-compress\': fullScreen.open}"></i></span>');
}]);
})();
