(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/iml/dashboard/assets/html/charts.html',
    '<div class="row dashboard" sorter="" items="dashboard.charts" key="\'chart\'"><div ng-repeat="chart in dashboard.charts track by chart.name"><div class="col-lg-6 dashboard-chart full-screen" sort-item=""><div ng-include="chart.name"></div></div></div></div>');
}]);
})();
