angular.module('charts').factory('appendDataTransformer', [appendDataTransformerFactory]);

/**
 * This transformer concatenates new data points with current data.
 */
function appendDataTransformerFactory() {
  'use strict';

  return function appendDataTransformer(resp) {
    var data = this.getter();

    if (!Array.isArray(data))
      throw new Error('Data not in expected format for appendDataTransformer!');

    if (!Array.isArray(resp.body))
      throw new Error('resp.body not in expected format for appendDataTransformer!');

    resp.body.forEach(function (newValSeries) {
      var series = _.find(data, {key: newValSeries.key});

      if (series == null) {
        data.push(newValSeries);
        return;
      }

      var params = [series.values.length, 0].concat(newValSeries.values);

      [].splice.apply(series.values, params);
    });

    return data;
  };
}


