(function(module) {
try {
  module = angular.module('iml');
} catch (e) {
  module = angular.module('iml', []);
}
module.run(['$templateCache', function($templateCache) {
  $templateCache.put('/static/chroma_ui/common/login/assets/html/eula.html',
    '<div class="modal-header"><h3>End User License Agreement Terms</h3></div><div class="modal-body eula"><div class="well" at-scroll-boundary="" one-hit="{{ true }}" ng-bind-html="eulaCtrl.eula"></div></div><div class="modal-footer"><button class="btn btn-success" ng-disabled="!hitBoundary" ng-click="eulaCtrl.accept()">Agree</button><button class="btn btn-danger" ng-click="eulaCtrl.reject()">Do Not Agree</button></div>');
}]);
})();
